.card {
  width: 300px;
  overflow: hidden;
  background-color: aqua;
  box-shadow: 0px 0px 15px -5px;
  margin: auto;
  margin-top: 20px;
  border-radius: 30px;
}

.card-image {
  overflow: hidden;
  height: 350px;
}

.card h3{
  font-size: 30px;
}

.heading {
  text-align: center;
}

.card p {
  font-size: 25px;
}

.card-contents{
  margin: 20px;
}